@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
#header {
  background-color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 20px;
}

#header__h1 {
  margin-left: 30px;
}

#header__button {
  color: white;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  padding: 6px 12px;
  margin-right: 40px;
  font-size: 14px;
  line-height: 1.42857143;
  border-radius: 4px;
}

.task {
  background: #eae8eb;
  margin-bottom: 10px;
  padding: 10px;
}

.task__main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.task__main--heading {
  margin: 0;
}

.task__date {
  margin: 5px 0 0;
}

.reminder {
  background: rgb(197, 204, 247);
}

.tasks {
  background: white;
  padding: 20px 20px 10px 20px;
}

.form {
  padding: 20px 30px 0;
  margin: auto;
  background-color: white;
}

.form__component {
  margin-bottom: 15px;
}

.form__component--label {
  display: inline-block;
  width: 20%;
  font-weight: 200;
}

.form__component--input {
  display: inline-block;
  width: 70%;
  border: 1px solid rgb(104, 104, 104);
  padding: 3px 10px;
}

.form__component--check {
  position: absolute;
  cursor: pointer;
  height: 18px;
  width: 18px;
}

.form__submit {
  color: white;
  background: dodgerblue;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  padding: 6px 12px;
  margin: auto;
  font-size: 14px;
  line-height: 1.42857143;
  border-radius: 4px;
}

.about {
  background: white;
  text-align: center;
  padding: 50px 0;
}

.about--heading {
  margin-top: 0;
}

.about--link {
  color: rgb(99, 99, 99);
  text-decoration: none;
}

.about--link:hover {
  color: black;
}

footer {
  text-align: center;
}

.footer__paragraph {
  margin: 20px 0 5px;
}

.link {
  color: rgb(99, 99, 99);
  text-decoration: none;
}

.link:hover {
  color: black;
}

body {
  font-family: sans-serif;
}

#container {
  background-color: #eae8eb;
  margin: auto;
  width: 50%;
  padding: 1.25em; /* 20px */
  margin-top: 100px;
}

